#story-page {
  position: relative;
  height: 100vh;

  #player-wrapper {
    position: absolute;
  }

  .progress-bar-wrapper {
    height: 30px;
    width: 100%;
    background-color: $black;
    position: absolute;
    bottom: 56px;
  }

  #progress-bar {
    margin: 0 auto;
  }

  .video-control-play {
    box-shadow: none;
    background-color: transparent;
    border: none;
    outline: none;

    &:focus {
      outline: none;
    }
  }

  #react-player {
    width: 100% !important;
    margin: 0 auto;
    height: calc(100vh - 56px) !important;

    @include tablet {
      height: auto !important;
    }
  }

  .controls {
    position: absolute;
    bottom: 10px;
    left: 134px;
    z-index: 1;
  }

  iframe {
    height: 100%;
  }

  .show {
    display: block !important;
  }

  .hide {
    display: none;
  }
}
